<template>
  <img src="https://zonaxie.com/img/energy1.svg" alt="" />
  <p>Rounds {{ round }}</p>
  <p>{{ counter }} / 10</p>
  <div class="controls">
    <Button @click="subsOne" text="-1 Energy" />
    <Button @click="addOne" text="+1 Energy" />
    <Button @click="nextRound" text="Next Round" />
    <Button @click="newGame" text="New Game" />
  </div>
  <div class="card-counter">
    <Button @click="addCards(-1)" text="-1 Card" />
    <Button @click="addCards(1)" text="+1 Card" />
  </div>
  <p>Total Cards:</p>
  <p>{{ cards }}</p>
</template>
<script>
import Button from "./Button.vue";
export default {
  name: "EnergyCalculator",
  components: {
    Button,
  },
  data() {
    return {
      round: 1,
      counter: 3,
      cards: 6,
    };
  },
  methods: {
    addOne() {
      this.counter < 10 ? this.counter++ : this.counter;
    },
    subsOne() {
      this.counter > 0 ? this.counter-- : 0;
    },
    nextRound() {
      this.round++;
      this.counter += 2;
      if (this.counter >= 10) {
        this.counter = 10;
      }
      this.addCards(3);
    },
    newGame() {
      this.round = 1;
      this.counter = 3;
      this.cards = 6;
    },
    addCards(amount) {
      this.cards += amount;
      if (this.cards > 12) this.cards = 12;
    },
  },
};
</script>

<style scoped>
img {
  max-width: 100%;
  width: 50px;
  border: 1px solid #2c3e50;
  border-radius: 50%;
}
.controls {
  display: flex;
  justify-content: space-evenly;
}
.card-counter {
  margin-top: 0.75rem;
}
p {
  margin-top: 0.5rem;
}
</style>
